*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  li{
    list-style: none;
  }
  a{
    color: inherit;
    text-decoration: none;
  }
  body{
    line-height: 1;
    color: #333;
}

.header{
    height: 100px;
}
.header .inner{
    max-width: 1280px;
    width: calc(100% - 30px);
    margin: 0 auto;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header .inner .nav .gnb{
    display: flex;
    gap: 50px;
}
.header .inner .nav_open{
    width: 20px;
    position: relative;
    z-index: 99;
}
.header .inner .nav_open img{
    width: 100%;
}
.all_nav{
    width: 50%;
    height: 100vh;
    position: fixed;
    top: 0;
    right: -100%;
    background-color: #555555;
    color: #fff;
}
.all_nav.on{
    right: 0;
}
.main_visual{
    display: flex;
    flex-direction: column;
}
.main_visual h2{
    margin-bottom: 50px;
}
.input_box input{
    width: 200px;
    height: 50px;
    border: 1px solid #333;
}
.input_box .text{
    margin-top: 10px;
    color: #fff;
}